@import './variables';

@mixin text($fontFamily, $fontSize, $fontWeight, $textColor) {
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  color: $textColor;
}

@mixin inputAndTextArea($color, $border, $borderRadius, $transition) {
  color: $color;
  border: $border;
  border-radius: $borderRadius;
  outline: none;
  transition: $transition;
}

@mixin button($width, $height, $backgroundColor, $borderColor) {
  width: $width;
  height: $height;
  background-color: $backgroundColor;
  overflow: hidden;
  border: 3px solid $borderColor;
  border-radius: 4px;
  margin-bottom: 20px;
  transition: .5s;
  @include text($fontFamilyStyle, 16px, 400, $textColor);
}

@mixin container() {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 80%;
  min-height: 100%;
  margin: 0 auto;
  padding: 40px 0;
  border-radius: 5px;
}